.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: -45px;
  width: 24px;
  height: 24px;
  display: block;
  background-image: url("../img/menu.svg");
  background-repeat: no-repeat;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  display: none;
}
.main-nav--closed .main-nav__toggle {
  display: block;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  display: block;
  background-image: url("../img/cross.svg");
  position: absolute;
  right: 20px;
  top: -45px;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #e6e6e6;
  /*display: none;/*/
}
